.todo{
   
        /* other styles */
       /* background: #edc0bf75; */
      /* background: linear-gradient(90deg, #c4caef  0,#edc0bfc0 58%);*/
      background: linear-gradient(90deg,  #fab596 0, #eca2e0 58%);


        -webkit-backdrop-filter: blur(10px);
        backdrop-filter: blur(10px);
    width: 400px;
    height: auto;
    padding: 2rem;
    border-radius: 1rem;
    box-shadow: 0 4px 6px #00000038;
    position:relative;


}

.todo-tittle{
    margin-top: 0;
    color: rgb(97, 133, 211) ;
  margin-bottom: .5rem;
  font-size: 1.2rem;
}

.todo-input{
    width: 80%;
    height: 40px;
    outline: none;
    border-radius: 8px;
    font-size: 15px ;
    cursor: pointer;
    border: 1px solid #edc0bf75;
    padding: 10px;
    box-sizing: border-box;
    box-shadow: 0 2px 4px #00000023;
    background: rgba(241, 241, 241, 0.685); 

    color: rgb(101, 119, 158);
}

.todo-i{
    width: 80%;
    height: 40px;
    margin-top: 15px;
    outline: none;
    border-radius: 8px;
    font-size: 15px ;
    cursor: pointer;
    border: 1px solid #edc0bf75;
    padding: 10px;
    box-sizing: border-box;
    box-shadow: 0 2px 4px #00000023;
    background: rgb(97, 133, 211) ;
    color: #eee5e5da ;
}

input::placeholder{
    color:  rgb(97, 133, 211) ;
}

ul{
   
    text-align: left;
    padding: 0;
    
}

ul li{
    list-style: none;
    background: linear-gradient(90deg, #eca2e0  0, #fab596 58%);

    color: rgb(98, 127, 190)  ;
    padding: 15px;
    border-radius: 5px;
    margin-bottom: 10px;
    box-shadow: 0 4px 6px #00000066;
    display: flex;
}

ul li i{
    text-align:right;
    margin-left: 0;
    flex: 1;
    cursor: pointer;
    color: rgb(98, 127, 190) ;
    

}